import './Seans.css'
import React, { useState } from 'react';
import { Link } from 'react-router-dom';


function Astroseans() {
  return (
    <div className="App">
      <Astroseans6 />
    </div>
  );
}


function Astroseans6() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isConfirmationModalOpen, setIsConfirmationModalOpen] = useState(false);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [selectedService, setSelectedService] = useState('');
  const [selectedServicePrice, setSelectedServicePrice] = useState(0);

  const openModal = (service, price) => {
    setSelectedService(service);
    setSelectedServicePrice(price);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const openConfirmationModal = () => {
    setIsConfirmationModalOpen(true);
  };

  const closeConfirmationModal = () => {
    setIsConfirmationModalOpen(false);
  };

  const handleNameChange = (event) => {
    setName(event.target.value);
  };

  const handleEmailChange = (event) => {
    setEmail(event.target.value);
  };

  const handleSubmit = () => {
    if (!isValidEmail(email)) {
      alert(',     ');
      return;
    }
    //         
    openConfirmationModal();
    //     
    closeModal();
  };

  const isValidEmail = (email) => {
    //      email
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  return (
    <div className='tovarnyblock'>
      <h1> </h1>
      <div className="tovary">
        <div className="tovar">
          <h2>-<br /><br /></h2>
          <p>  ,       </p>
          <p>      </p>
          <p>     </p>
          <h2>: 1000 /1,5 </h2>
          <button onClick={() => openModal('-', 1000)}>  </button>
        </div>
        <div className="tovar">
          <h2>   </h2>
          <p>  ,       </p>
          <p>      </p>
          <p>     </p>
          <h2>: 2500 /1,5 </h2>
          <button onClick={() => openModal('   ', 2500)}>  </button>
        </div>
        <div className="tovar">
          <h2>  </h2>
          <p>   ,    </p>
          <p>     </p>
          <p>       </p>
          <h2>: 1500 /1,5 </h2>
          <button onClick={() => openModal('  ', 1500)}>  </button>
        </div>
      </div>

      {/*    */}
      {isModalOpen && (
        <div className="modal">
          <div className="modal-content">
            <span className="close" onClick={closeModal}>&times;</span>
            <h1 className="modal-title"> </h1>
            <p> : {selectedService}</p>
            <p>: {selectedServicePrice} /1,5 </p>
            <p>   :</p>
            <input type="text" placeholder="" value={name} onChange={handleNameChange} className="input-field" />
            <input type="email" placeholder="Email" value={email} onChange={handleEmailChange} className="input-field" />
            <button onClick={handleSubmit} disabled={!name || !email}> </button>
          </div>
        </div>
      )}

      {/*    */}
      {isConfirmationModalOpen && (
        <div className="modal">
          <div className="modal-content">
            <span className="close" onClick={closeConfirmationModal}>&times;</span>
            <h1 className="modal-title" >     !</h1>
            <p>        <br />     : <br /> {email}</p>
            <Link to="/">
              <button onClick={() => { setIsConfirmationModalOpen(false); setIsModalOpen(false); }}>   </button>
            </Link>
          </div>
        </div>
      )}
    </div>
  );
}
  
export default Astroseans;


