import "./School.css";
import React, { useState } from "react";

function Schoolcola() {
    return (
      <div className="App">
        <InfoGroup />
        <Sert />
        <Podpiska />
      </div>
    );
  }

function InfoGroup(){
    return (
      <section className="info-group" >
        <div className="info-content">
          <h2></h2>
          <div className="group-description">
            <p className="paragraph">  Astrotips      .           ,    -   .</p>
            <p className="paragraph">        .     ,        ,    .          .</p>
            <p className="paragraph"> ,   ,     ,      .      ,      .
               -         . </p>
            <p className="paragraph"> Astrotips   - .          .</p>
            <h1>    :</h1>
            <p className="paragraph">  :       ,     .<br />
             :          .<br />
             :  - ,  ,        .<br />
             :        .</p>
            <p className="paragraph">        .  -          .
                    .           .         .
                .     -,     .</p>
            <h3>         !</h3>
          </div>
        </div>
      </section>
    )
  }

function Sert(){
    return(
      <div className="sert">
        <div className="text-sert">
          <h1>     </h1>
        </div>
        <div className="image-school">
            <img className="image-schl" src="murzilka.webp" alt=""  />
        </div>
      </div>
    )
}


const Podpiska = () => {
  //   ,      
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  const [modalVisible, setModalVisible] = useState(false); //     

  //      
  const handleNameChange = (e) => {
    setName(e.target.value);
    validateButton();
  };

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
    validateButton();
  };

  //   ,    
  const validateButton = () => {
    const isValidName = name.length > 0;
    const isValidEmail = email.includes("@") && email.includes(".");
    setIsButtonDisabled(!isValidName || !isValidEmail);
  };

  //   
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!isButtonDisabled) {
      //   alert  ,    
      setModalVisible(true);
    }
  };

  return (
    <section className="Rassylka1">
      <h2> ,    !</h2>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          placeholder=" "
          value={name}
          onChange={handleNameChange}
        />
        <input
          type="email"
          placeholder=" "
          value={email}
          onChange={handleEmailChange}
        />
        <button type="submit" disabled={isButtonDisabled}>
          
        </button>
      </form>

      {/*   */}
      {modalVisible && (
        <div className="modal">
          <div className="modal-content">
            <span className="close" onClick={() => setModalVisible(false)}>
              &times;
            </span>
            <p>   ! <br />      <br />    </p>
          </div>
    </div>
  )}
</section>
);
};






export default Schoolcola;